import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
var NumberFormat = require('react-number-format');

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      cryptos: []

    };
  }

  componentDidMount() {
    axios.get('https://min-api.cryptocompare.com/data/pricemulti?fsyms=BTC,DASH,XLM,LTC,LSK,NEO,ING,ETC,XMR,XRP,ETH&tsyms=USD')
      .then(res => {
        const cryptos = res.data;
        console.log(cryptos);
        this.setState({cryptos: cryptos});
      })
  }








  render() {
    return (
      
      <div className="App">
      {Object.keys(this.state.cryptos).map((key) => (

          <div id= "crypto-container">
          <div class="fluid-container col-xs-6">
          
            <span className="left">{key}</span>
            <span className="right"><NumberFormat value ={this.state.cryptos[key].USD} displayType={'text'} 
            decimalPrecision={2} thousandSeparator={true} prefix={'$'} /></span>
            </div>
</div>
        ))}
      </div>
    );
  }
}
export default App;



